import { useMutation } from '@apollo/client';
import React, { useEffect, useState } from 'react';
import { MdEdit, MdInfo, MdMonetizationOn, MdPersonalVideo } from 'react-icons/md';
import { connect } from 'react-redux';
import { Link, useParams } from 'react-router-dom';
import { BasicScreenInner } from '../../components/BasicScreen';
import { Block, BlockTitle } from '../../components/Block';
import ErrorPage from '../../components/ErrorPage';
import Footer from '../../components/Footer';
import { Button, Form, Input, Textarea } from '../../components/Form';
import LoadingPage from '../../components/LoadingPage';
import { Modal } from '../../components/Modal';
import Playlist from '../../components/Playlist';
import { CREATE_ACTIVITIES_BLOCK, CREATE_BLOCK, CREATE_LESSON, UPDATE_COURSE, WATCH } from '../../graphql/mutations';
import { FETCH_COURSE, FETCH_USER } from '../../graphql/queries';
import { setUser } from '../../store/actions';
import { Content, Course, Activity, Block as BlockType, ActivitiesBlock } from '../../types/Course';
import { User } from '../../types/User';
import ActivityPage from '../Activity';
import convertTextToHtml from '../Activity/convertTextToHtml';
import Video from '../Video';

import './styles.css';

export interface CreateBlockModalProps {
  course: Course,
  onSubmitted?: (course: Course) => void,
  onExitClick: () => void
}

export interface CreateLessonModalProps {
  block: BlockType,
  onSubmitted?: (course: Course) => void,
  onExitClick: () => void
}

export interface CreateActivitiesBlockModalProps {
  block: BlockType,
  onSubmitted?: (course: Course) => void,
  onExitClick: () => void
}
 
const CreateBlockModal: React.FC<CreateBlockModalProps> = ({ onExitClick, onSubmitted, course }) => {
  const [name, setName] = useState('');
  const [loading, setLoading] = useState(false);

  const [createBlock] = useMutation(CREATE_BLOCK);

  const onSubmit = async (e: any) => {
    e.preventDefault();

    if(loading) return;

    try {
      setLoading(true);

      const response = await createBlock({ variables: { name, course_id: Number(course.id) } });

      /* @ts-ignore */
      onSubmitted(response.data.createBlock);
    } catch(error: any) {
      setLoading(false);
      if(error.message === 'Unexpected token < in JSON at position 1')
        alert('Erro ao Conectar ao Servidor');
      else
        alert(error.message);
    }
  }

  return (
  <Modal className="course-modal" onExitClick={onExitClick}>
    <BlockTitle title="Criar Bloco" />
    <Form onSubmit={onSubmit}>
      <Input Icon={MdInfo} placeholder="Nome do Bloco" type="text" onChangeText={setName} value={name} required={true} />
      { loading ? <Button name="Criando.." type="button" onClick={() => {}} /> : <Button type="submit" name="Criar" /> }
    </Form>
  </Modal>
  );
}

const CreateLessonModal: React.FC<CreateLessonModalProps> = ({ onExitClick, onSubmitted, block }) => {
  const [name, setName] = useState('');
  const [youtubeLink, setYoutubeLink] = useState('');
  const [description, setDescription] = useState('');

  const [loading, setLoading] = useState(false);

  const [createLesson] = useMutation(CREATE_LESSON);

  const onSubmit = async (e: any) => {
    e.preventDefault();

    if(loading) return;

    try {
      setLoading(true);

      const response = await createLesson({ variables: { name, youtube_link: youtubeLink, block_id: Number(block.id) } });

      /* @ts-ignore */
      onSubmitted(response.data.createLesson);
    } catch(error: any) {
      setLoading(false);
      if(error.message === 'Unexpected token < in JSON at position 1')
        alert('Erro ao Conectar ao Servidor');
      else
        alert(error.message);
    }
  }

  return (
  <Modal className="course-modal" onExitClick={onExitClick}>
    <BlockTitle title="Criar Vídeoaula" />
    <Form onSubmit={onSubmit}>
      <Input Icon={MdInfo} placeholder="Título" type="text" onChangeText={setName} value={name} required={true} />
      <Input Icon={MdInfo} placeholder="Link do Video do Youtube" type="text" onChangeText={setYoutubeLink} value={youtubeLink} required={true} />
      <Textarea placeholder="Descrição" onChangeText={setDescription} value={description}  />
      { loading ? <Button name="Criando.." type="button" onClick={() => {}} /> : <Button type="submit" name="Criar" /> }
    </Form>
  </Modal>
  );
}

const CreateActivitiesBlockModal: React.FC<CreateActivitiesBlockModalProps> = ({ onExitClick, onSubmitted, block }) => {
  const [name, setName] = useState('');

  const [loading, setLoading] = useState(false);

  const [createActivitiesBlock] = useMutation(CREATE_ACTIVITIES_BLOCK);

  const onSubmit = async (e: any) => {
    e.preventDefault();

    if(loading) return;

    try {
      setLoading(true);

      const response = await createActivitiesBlock({ variables: { name, block_id: Number(block.id) } });

      /* @ts-ignore */
      onSubmitted(response.data.createActivitiesBlock);
    } catch(error: any) {
      setLoading(false);
      if(error.message === 'Unexpected token < in JSON at position 1')
        alert('Erro ao Conectar ao Servidor');
      else
        alert(error.message);
    }
  }

  return (
  <Modal className="course-modal" onExitClick={onExitClick}>
    <BlockTitle title="Criar Bloco de Exercícios" />
    <Form onSubmit={onSubmit}>
      <Input Icon={MdInfo} placeholder="Título" type="text" onChangeText={setName} value={name} required={true} />
      { loading ? <Button name="Criando.." type="button" onClick={() => {}} /> : <Button type="submit" name="Criar" /> }
    </Form>
  </Modal>
  );
}

const CoursePage: React.FC<any> = ({ history, dispatch, user }) => {
  const [course, setCourse] = useState<Course | null>(null);
  const [blockSelected, setBlockSelected] = useState<BlockType | null>(null);
  
  const [isCreateBlockModalOpen, setIsCreateBlockModalOpen] = useState(false);
  const [isCreateLessonModalOpen, setIsCreateLessonModalOpen] = useState(false);
  const [isCreateActivitiesBlockModalOpen, setIsCreateActivitiesBlockModalOpen] = useState(false);
  const [loadingPublish, setLoadingPublish] = useState(false);

  const [loading, setLoading] = useState(true);
  const [isAuthor, setIsAuthor] = useState(false);
  const [error, setError] = useState('');

  const [lessonsNumber, setLessonsNumber] = useState(0);
  const [activitiesNumber, setActivitiesNumber] = useState(0);

  const [contentSelected, setContentSelected] = useState<Content | null>(null);

  const [show, setShow] = useState(false);
  
  const [fetchUser] = useMutation(FETCH_USER);
  const [fetchCourse] = useMutation(FETCH_COURSE);
  const [watch] = useMutation(WATCH);
  const [updateCourse] = useMutation(UPDATE_COURSE);
  
  const { id } = useParams<any>();

  useEffect(() => {
    userVerify();
  }, []);

  const userVerify = async () => {
    if(!localStorage.getItem('token')) history.push('/');
    
    try {
      let account = user;

      if(!account) {
        const responseWebAccount = await fetchUser();
        const { userMutation } = responseWebAccount.data;
        account = userMutation;
      }

      const responseCourse = await fetchCourse({ variables: { id: Number(id) } });
      const { courseMutation } = responseCourse.data;

      dispatch(setUser(account));
      setCourse(courseMutation);

      let l = 0;
      let a = 0;

      /* @ts-ignore */
      courseMutation.blocks.forEach((block: Block) => {
        block.contents.forEach((content: Content) => {
          if(content.lesson)
            l += 1;
          else
            a += 1;
        });
      });

      courseMutation.teachers.forEach((teacher: User) => {
        if(account.id == teacher.id) setIsAuthor(true);
      });

      setLessonsNumber(l);
      setActivitiesNumber(a);

      setLoading(false);      
    } catch(error: any) {
      console.log(error);
      setLoading(false); 
      setError('Erro ao conectar com o servidor');
    }
  }


  const onWatchChange = (watched: boolean) => {
    const newCourse = course;
    const newContentSelected = contentSelected;

    /* @ts-ignore */
    newContentSelected.watched = watched;

    setContentSelected(newContentSelected);

    /* @ts-ignore */
    for(let i = 0; i < newCourse?.blocks.length; i++) {
      const contents = newCourse?.blocks[i].contents;
      /* @ts-ignore */
      for(let j = 0; j < contents.length; j++) {
        /* @ts-ignore */
        if(contents[j].id == contentSelected.id) {
          /* @ts-ignore */
          newCourse.blocks[i].contents[j].watched = watched;
          /* @ts-ignore */
          setCourse({...newCourse});
          return;
        }
      }
    }
  }

  const onUserSubmit = (content: Content, activityId: number, userAnswerId: number) => {
    const newCourse = course;
    let newContentSelected = contentSelected;

    /* @ts-ignore */
    newContentSelected.activitiesblock.activities = newContentSelected.activitiesblock.activities.map((activity: Activity) => {
      if(activity.id == activityId) activity.user_answer_id = userAnswerId;
      return activity;
    });

    /* @ts-ignore */
    setContentSelected({...newContentSelected});

    /* @ts-ignore */
    for(let i = 0; i < newCourse?.blocks.length; i++) {
      const contents = newCourse?.blocks[i].contents;
      /* @ts-ignore */
      for(let j = 0; j < contents.length; j++) {
        /* @ts-ignore */
        if(contents[j].id == contentSelected.id) {
          /* @ts-ignore */
          newCourse.blocks[i].contents[j] = newContentSelected;
          /* @ts-ignore */
          setCourse({...newCourse});
          return;
        }
      }
    }  
  }

  const setPublish = async (published: boolean) => {
    if(loadingPublish) return;

    try {
      setLoadingPublish(true);

      const response = await updateCourse({ variables: { id: Number(id), published } });
      setCourse(response.data.updateCourse);

      setLoadingPublish(false);
    } catch(error: any) {
      setLoadingPublish(false);
      if(error.message === 'Unexpected token < in JSON at position 1')
        alert('Erro ao Conectar ao Servidor');
      else
        alert(error.message);
    }
  }
 
  const onBlockSubmitted = (course: Course) => {
    setIsCreateBlockModalOpen(false);
    setCourse(course);
  }

  const onLessonSubmitted = (course: Course) => {
    setIsCreateLessonModalOpen(false);
    setCourse(course);
  }

  const onActivitiesBlockSubmitted = (course: Course) => {
    setIsCreateActivitiesBlockModalOpen(false);
    setCourse(course);
  }

  const onNewActivitySubmitted = (course: Course) => {
    setCourse({...course});
    let content = null;
    course.blocks.forEach((block: BlockType) => {
      block.contents.forEach((content: Content) => {
        /* @ts-ignore */
        if(content.id == contentSelected.id) setContentSelected({...content});
      });
    });
  }

  const onDeleteActivity = (activitiesBlock: ActivitiesBlock) => {
    /* @ts-ignore */
    const blocks = course.blocks.map((block: BlockType) => {
      block.contents = block.contents.map((content: Content) => {
        if(content.activitiesblock)
          if(content.activitiesblock.id == activitiesBlock.id) {
            content.activitiesblock = activitiesBlock;
            setContentSelected({...content});
          }
        return content;
      });
      return block;
    });

    /* @ts-ignore */
    setCourse({...course, blocks});
  }

  const onContentClick = async (content: Content) => {
    setContentSelected(content);
    try {
      if(content.lesson) await watch({ variables: { id: Number(content.lesson.id) } });
    } catch(error) {
      console.log(error);
    }
  }

  if(error) return <ErrorPage text={error} />;
  if(loading) return <LoadingPage />;

  if(contentSelected) {
    if(contentSelected.lesson) {
      return <Video content={contentSelected} course={course} onContentClick={onContentClick} onWatchChange={onWatchChange} />;
    }

    if(contentSelected.activitiesblock) {
      return (
        <>
          <ActivityPage
           content={contentSelected}
           course={course}
           isAuthor={isAuthor}
           onContentClick={onContentClick}
           onUserSubmit={onUserSubmit}
           onNewActivitySubmitted={onNewActivitySubmitted}
           onDeleteActivity={onDeleteActivity} />
        </>
      );
    }
  }

  let totalLessons = 0;
  let totalActivities = 0;

  if(!show)
    return (
      <BasicScreenInner className="course">
        <div className="course-image" style={{backgroundImage: `url(${course?.course_image})` }}>
          <div className="course-container">
          <div className="course-info">
            <div className="course-title">
              { /* @ts-ignore */ }
              <h2>{ course?.name }</h2>
              <span>{ course?.institute.name }</span>
              <p>
              { course?.copy }
              </p>
            </div>
            <img src={course?.institute.white_image} />
          </div>

          <div className="course-get">
            { isAuthor ? <BlockTitle title="Modificar" /> : null }
            { isAuthor ? <p>Clique abaixo para modificar o seu curso, como adicionar novos vídeos e exercícios.</p> : null }
            { isAuthor ? <Button name="Modificar" onClick={() => setShow(true)} /> : null }

            { !isAuthor && !course?.started ? <BlockTitle title="Começar Curso" /> : null }
            { !isAuthor && !course?.started ? <p>Clique abaixo para se inscrever no curso e começa-lo. Totalmente gratuito, como o conhecimento deve ser.</p> : null }
            { !isAuthor && !course?.started ? <Button name="Inscrever" onClick={() => setShow(true)} /> : null }

            { !isAuthor && course?.started ? <BlockTitle title="Continuar Curso" /> : null }
            { !isAuthor && course?.started ? <p>Clique abaixo para continuar o curso do mesmo ponto que você parou.</p> : null }
            { !isAuthor && course?.started ? <Button name="Continuar" onClick={() => setShow(true)} /> : null }
          </div>
          </div>
        </div>
        <div className="course-body">
          <div className="course-click">
          <a>Introdução</a>
          { course?.requirements ? <a>Requisitos</a> : null }
          <a>Autor</a>
          <a>Instituição</a>
          </div>
          <div className="course-body-two-container"> 
          <div className="course-body-container">
            <div className="course-body-text">
            <h2 id="introducao">Sobre o Curso</h2>
            { course ? <div className="activity-text" dangerouslySetInnerHTML={{__html: convertTextToHtml(course?.description)}} /> : null }

            { course?.requirements ? <h2 id="requisitos">Pré-Requisitos</h2> : null }
            { course?.requirements? <div className="activity-text" dangerouslySetInnerHTML={{__html: convertTextToHtml(course?.requirements)}} /> : null }

            <h2>Grade Curricular</h2>
            
            { course?.blocks.map((block: BlockType) => {
              let lessonsNumber = 0;
              let activitiesNumber = 0;

              block.contents.forEach((content: Content) => {
                if(content.lesson) return lessonsNumber += 1;
                /* @ts-ignore */
                activitiesNumber += content.activitiesblock?.exercises_number;
              });

              totalLessons += lessonsNumber;
              totalActivities += activitiesNumber;

              return (
              <div className="curricular-item">
                <h3>{ block.name }</h3>
                { lessonsNumber ? <span>{ lessonsNumber } Aulas disponíveis</span> : null }
                { activitiesNumber ? <span>{ activitiesNumber } Atividades disponíveis</span> : null }
              </div>
              );
            }) }

            <h2 id="autor">Autor</h2>
            { course?.teachers.map((teacher: User) => 
              <div key={`teacher${teacher.id}`} className="course-teacher">
                <img src={teacher.photo_url} />
                <div>
                  <span onClick={() => history.push(`/cursos-professor/${teacher.id}`)}>Prof. {teacher.name}</span>
                  <p>
                  { teacher.description }
                  </p>
                </div>
              </div>) }

            <h2 id="instituticao">Instituição</h2>
            <div className="course-teacher course-institute">
              <img src={course?.institute.image} />
              <div>
                <span>{course?.institute.name}</span>
                <p>
                { course?.institute.description }
                </p>
              </div>
            </div>

            </div>
            <div className="course-body-info">
              <div className="course-body-info-item">
                <MdMonetizationOn />
                <div>
                  <span>Gratuito</span>
                  <p>O curso é totalmente gratuito, não é necessário fazer nenhum pagamento para ter acesso ao curso.</p>
                </div>
              </div>
              
              { totalLessons ? <div className="course-body-info-item">
                <MdPersonalVideo />
                <div>
                  <span>{ totalLessons } Aulas</span>
                  <p>Que você pode acessar a qualquer hora e de qualquer lugar.</p>
                </div>
              </div> : null }
              
              { totalActivities ? <div className="course-body-info-item">
                <MdEdit />
                <div>
                  <span>{ totalActivities } Exercícios</span>
                  <p>Disponibilizados de maneira dinâmica dentro da plataforma, especialmente para você.</p>
                </div>
              </div> : null }

              <div className="course-body-info-item">
                <MdEdit />
                <div>
                  <span>Online</span>
                  <p>Completamente online, faça ao seu tempo, sem que você precise ir para nenhum lugar presencialmente.</p>
                </div>
              </div>
            </div>
          </div>
          </div>
        </div>
        <Footer />
      </BasicScreenInner>
    );


  return (
    <BasicScreenInner className="course-show">
      <Block width="50%" className="course-block">
        <img src={course?.course_image} />
        <div className="course-info">
          <h2>{ course?.name }</h2>
          <span>{lessonsNumber} Aulas Disponíveis</span>
          { activitiesNumber ? <span>{activitiesNumber} Exercícios</span> : null }
          <span>{ course?.institute.name }</span>
          { /* @ts-ignore */ }
          { isAuthor ? course.published ? <span  onClick={() => setPublish(false)} className="course-publish">Despublicar Curso</span> : <span className="course-publish" onClick={() => setPublish(true)}>Publicar Curso</span> : null }
          { course?.teachers.map((teacher: User) => 
            <div key={`teacher${teacher.id}`} onClick={() => history.push(`/cursos-professor/${teacher.id}`)} className="course-teacher">
              <span>Prof. {teacher.name}</span>
            </div>) }
        </div>
      </Block>

      <Playlist
        blocks={course?.blocks}
        onContentClick={onContentClick}
        onCreateBlockClick={() => setIsCreateBlockModalOpen(true)}
        onCreateLessonClick={(block: BlockType) => { setIsCreateLessonModalOpen(true); setBlockSelected(block) }}
        onCreateActivitiesBlockClick={(block: BlockType) => { setIsCreateActivitiesBlockModalOpen(true); setBlockSelected(block) }}
        onDeleteContent={setCourse}
        isAuthor={isAuthor} />

      { /* @ts-ignore */ }
      { loadingPublish ? <Modal><BlockTitle title={course.published ? `Despublicando Curso..` : `Publicando Curso..`} /></Modal> : null }

      { /* @ts-ignore */ }
      { isCreateBlockModalOpen ? <CreateBlockModal course={course} onExitClick={() => setIsCreateBlockModalOpen(false)} onSubmitted={onBlockSubmitted} /> : null }
      { /* @ts-ignore */ }
      { isCreateLessonModalOpen ? <CreateLessonModal block={blockSelected} onExitClick={() => setIsCreateLessonModalOpen(false)} onSubmitted={onLessonSubmitted} /> : null }
      { /* @ts-ignore */ }
      { isCreateActivitiesBlockModalOpen ? <CreateActivitiesBlockModal block={blockSelected} onSubmitted={onActivitiesBlockSubmitted} onExitClick={() => setIsCreateActivitiesBlockModalOpen(false)} /> : null }
    </BasicScreenInner>
  );
}

const mapStateToProps = (state: any) => {
  const { user: { idCampus, user } } = state;
  return { idCampus, user };
};

export default connect(mapStateToProps)(CoursePage);