import { useMutation } from '@apollo/client';
import React, { useEffect, useRef, useState } from 'react';
import { connect } from 'react-redux';
import { useParams } from 'react-router-dom';
import { Block, BlockTitle } from '../../components/Block';
import ErrorPage from '../../components/ErrorPage';
import { FETCH_USER, SEARCH } from '../../graphql/queries';
import { setUser } from '../../store/actions';
import { Course, Topic, SubTopic } from '../../types/Course';
import { User } from '../../types/User';

import './styles.css';

const Search: React.FC<any> = ({ history, dispatch, user }) => {
  const [topics, setTopics] = useState<Topic[]>([]);
  const [subtopics, setSubtopics] = useState<SubTopic[]>([]);
  const [courses, setCourses] = useState<Course[]>([]);
  const [teachers, setTeachers] = useState<User[]>([]);

  const [start, setStart] = useState<number>(0);

  const [loading, setLoading] = useState(true);
  const [loadingNews, setLoadingNews] = useState(true);
  const [listEnded, setListEnded] = useState(false);

  const [error, setError] = useState('');
  
  const [fetchUser] = useMutation(FETCH_USER);
  const [fetchSearch] = useMutation(SEARCH);
  
  const { query } = useParams<any>();

  const listRef = useRef(null);

  useEffect(() => {
    setLoading(true);
    setStart(0);
    userVerify();
  }, [query]);

  const userVerify = async () => {
    if(!localStorage.getItem('token')) history.push('/');
    
    try {
      let account = user;

      if(!account) {
        const responseWebAccount = await fetchUser();
        const { userMutation } = responseWebAccount.data;
        account = userMutation;
      }

      const responseCourse = await fetchSearch({ variables: { search: query, start: 0 } });
      const { search } = responseCourse.data;

      setStart(10);

      dispatch(setUser(account));

      setTopics(search.topics);
      setSubtopics(search.subtopics);
      setCourses(search.courses);
      setTeachers(search.teachers);

      if(search.courses.length < 10) setListEnded(true);

      setLoading(false);
      setLoadingNews(false);    
    } catch(error: any) {
      setLoading(false); 
      setError('Erro ao conectar com o servidor');
    }
  }

  useEffect(() => {
    if(!listRef) return;
    if(!listRef.current) return;
    /* @ts-ignore */
    listRef.current.addEventListener('scroll', (e) => {
      const el = e.target;
      /* @ts-ignore */
      if(el.scrollTop + el.clientHeight === el.scrollHeight) load();
    });  
  }, [listRef.current]);

  const load = async () => {
    if(loadingNews) return;
    if(listEnded) return;
    setLoadingNews(true);
    try {
        const responseCourse = await fetchSearch({ variables: { search: query, start } });
        const { search } = responseCourse.data;

      setStart(start + 10);
      /* @ts-ignore */
      setCourses([...courses, ...search.courses]);

      if(search.courses.length < 10) {
        setListEnded(true);
      }

      setLoadingNews(false);      
    } catch(error: any) {
      setLoadingNews(false); 
      setError('Erro ao conectar com o servidor');
    }
  }

  if(error) return <ErrorPage text={error} />;
  if(loading) return <div className={`basic-screen-inner search`}><Block width="90%" className="loading-block"><BlockTitle title="Pesquisando.." /></Block></div>;
  if(!loading && !topics.length && !courses.length) return <div className={`basic-screen-inner search`}><Block width="90%" className="loading-block"><BlockTitle title="Pesquisa Não Retornou Nenhum Valor" /></Block></div>;

  return (
    <div className={`basic-screen-inner search`} ref={listRef}>
      <div style={{ marginTop: '24px' }}>
        <BlockTitle title={`Resultados para "${query}"`} />
      </div>
      { topics.map((topic: Topic) => (
          <Block width="90%" className="course-vertical-block topic-vertical-block" onClick={() => history.push(`/topico/${topic.id}`)}>
            <span>Tópico</span>
            <BlockTitle title={topic.name} />
            <span>{ topic.courses_number } Cursos Disponíveis</span>
          </Block>
        )) }

      { subtopics.map((subtopic: SubTopic) => (
          <Block width="90%" className="course-vertical-block topic-vertical-block" onClick={() => history.push(`/sub-topico/${subtopic.id}`)}>
            <span>Sub Tópico</span>
            <BlockTitle title={subtopic.name} />
          </Block>
        )) }

      { teachers.map((teacher: User) => (
          <Block width="90%" className="course-vertical-block topic-vertical-block" onClick={() => history.push(`/cursos-professor/${teacher.id}`)}>
            <span>Professor</span>
            <BlockTitle title={teacher.name} />
          </Block>
        )) }

      { courses.map((course: Course) => (
          <Block width="90%" className="course-vertical-block" onClick={() => history.push(`/curso/${course.id}`)}>
            <div className="course-item-list-image" style={{ backgroundImage: `url(${course.course_image})`, borderRadius: '12px' }}>
              <div className="course-item-list-university">
                <img src={course.institute.image} />
              </div>
            </div>
            <div className="info">
              <b>{course.name}</b>
              <span>{ course.institute.name }</span>
              <span>{ course.copy }</span>
            </div>
          </Block>
        )) }
    </div>
  );
}

const mapStateToProps = (state: any) => {
  const { user: { idCampus, user } } = state;
  return { idCampus, user };
};

export default connect(mapStateToProps)(Search);