import { useEffect, useState } from 'react';
import { MdExitToApp, MdHome, MdSearch } from 'react-icons/md';
import { connect } from 'react-redux';
import { useHistory } from 'react-router';
import { Button } from '../Form';
import './styles.css';

interface BasicScreenProps {
  children: React.ReactNode,
  className?: string,
  ref?: any,
  user?: any
}

let BasicScreen: React.FC<BasicScreenProps> = ({ user, children }) => {
  const [search, setSearch] = useState('');
  const history = useHistory();

  const exit = () => {
    localStorage.setItem('token', '');
    history.push('/');
  }

  useEffect(() => {
    const delayDebounceFn = setTimeout(() => {
      if(search) history.push(`/pesquisar/${search}`);
    }, 500)

    return () => clearTimeout(delayDebounceFn)
  }, [search])

  return (
    <div className="basic-screen">
      <div className="basic-screen-top">
        <h1 onClick={() => history.push('/inicio')}>Instrue</h1>
        <div className="basic-screen-search-container">
          <button className="basic-screen-explore-button" onClick={() => history.push(`/inicio/todos/`)}>Explorar</button>
          <div className="basic-screen-search">
            <form onSubmit={e => e.preventDefault()}>
              <input type="text" placeholder="Procurar" value={search} onChange={e => setSearch(e.target.value)} />
              <button type="submit">
                <MdSearch size={30} />
              </button>
            </form>
          </div>
        </div>
        <div className="basic-screen-top-right">
          { user ? user.teacher_verified && <span onClick={() => history.push(`/cursos-professor/${user.id}`)}>Meus Cursos</span> : null}
          <span onClick={exit}>Sair</span>
          <img src={user ? user.photo_url : ''} className="basic-screen-avatar" />
        </div>
      </div>
      { children }
    </div>
  );
}

const BasicScreenInner: React.FC<BasicScreenProps> = ({ children, className, ref }) => {
  return (
    <div className={`basic-screen-inner ${className}`} ref={ref}>
      { children }
    </div>
  );
}

const mapStateToProps = (state: any) => {
  const { user: { idCampus, user } } = state;
  return { idCampus, user };
};

BasicScreen = connect(mapStateToProps)(BasicScreen);

export { BasicScreen, BasicScreenInner };