import { useMutation } from '@apollo/client';
import React, { useEffect, useRef, useState } from 'react';
import { connect } from 'react-redux';
import { useParams } from 'react-router-dom';
import { Block, BlockTitle } from '../../components/Block';
import ErrorPage from '../../components/ErrorPage';
import LoadingPage from '../../components/LoadingPage';
import { FETCH_COURSES_BY_SUBTOPIC, FETCH_USER } from '../../graphql/queries';
import { setUser } from '../../store/actions';
import { Course } from '../../types/Course';


const SubtopicPage: React.FC<any> = ({ history, dispatch, user }) => {
  const [courses, setCourses] = useState<Course[]>([]);
  const [start, setStart] = useState<number>(0);

  const [loading, setLoading] = useState(true);
  const [loadingNews, setLoadingNews] = useState(true);
  const [listEnded, setListEnded] = useState(false);

  const [error, setError] = useState('');
  
  const [fetchUser] = useMutation(FETCH_USER);
  const [fetchCoursesBySubtopic] = useMutation(FETCH_COURSES_BY_SUBTOPIC);
  
  const { id } = useParams<any>();

  const listRef = useRef(null);

  useEffect(() => {
    userVerify();
  }, []);

  const userVerify = async () => {
    if(!localStorage.getItem('token')) history.push('/');
    
    try {
      let account = user;

      if(!account) {
        const responseWebAccount = await fetchUser();
        const { userMutation } = responseWebAccount.data;
        account = userMutation;
      }

      const responseCourse = await fetchCoursesBySubtopic({ variables: { id: Number(id), start } });
      const { coursesBySubtopic } = responseCourse.data;

      setStart(10);

      dispatch(setUser(account));
      setCourses(coursesBySubtopic);

      if(coursesBySubtopic.length < 10) setListEnded(true);

      setLoading(false);
      setLoadingNews(false);    
    } catch(error: any) {
      setLoading(false); 
      setError('Erro ao conectar com o servidor');
    }
  }

  useEffect(() => {
    if(!listRef) return;
    if(!listRef.current) return;
    /* @ts-ignore */
    listRef.current.addEventListener('scroll', (e) => {
      const el = e.target;
      /* @ts-ignore */
      if(el.scrollTop + el.clientHeight === el.scrollHeight) load();
    });  
  }, [listRef.current]);

  const load = async () => {
    if(loadingNews) return;
    if(listEnded) return;
    setLoadingNews(true);
    try {
        const responseCourse = await fetchCoursesBySubtopic({ variables: { id: Number(id), start } });
        const { coursesBySubtopic } = responseCourse.data;

      setStart(start + 10);
      /* @ts-ignore */
      setCourses([...courses, ...coursesBySubtopic]);

      if(coursesBySubtopic.length < 10) {
        setListEnded(true);
      }

      setLoadingNews(false);      
    } catch(error: any) {
      setLoadingNews(false); 
      setError('Erro ao conectar com o servidor');
    }
  }

  if(error) return <ErrorPage text={error} />;
  if(loading) return <LoadingPage />;

  return (
    <div className={`basic-screen-inner topic`} ref={listRef}>
      { !courses.length ?
      <div style={{ marginTop: '24px' }}>
        <BlockTitle title={`Sem Resultados`} />
      </div>
      : null }
      { courses.map((course: Course) => (
          <Block width="90%" className="course-vertical-block" onClick={() => history.push(`/curso/${course.id}`)}>
            <div className="course-item-list-image" style={{ backgroundImage: `url(${course.course_image})`, borderRadius: '12px' }}>
              <div className="course-item-list-university">
                <img src={course.institute.image} />
              </div>
            </div>
            <div className="info">
              <b>{course.name}</b>
              <span>{ course.institute.name }</span>
              <span>{ course.copy }</span>
            </div>
          </Block>
        )) }
    </div>
  );
}

const mapStateToProps = (state: any) => {
  const { user: { idCampus, user } } = state;
  return { idCampus, user };
};

export default connect(mapStateToProps)(SubtopicPage);