import { useMutation } from '@apollo/client';
import { ScrollingCarousel } from '@trendyol-js/react-carousel';
import { useEffect, useRef, useState } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { BasicScreenInner } from '../../components/BasicScreen';
import { Block, BlockTitle } from '../../components/Block';
import { CourseItemList, CourseList } from '../../components/CourseList';
import ErrorPage from '../../components/ErrorPage';
import LoadingPage from '../../components/LoadingPage';
import { FETCH_FEED_ALL, FETCH_USER } from '../../graphql/queries';
import { setUser } from '../../store/actions';
import { Course, BlockFeed } from '../../types/Course';
import LeftIcon from '../Start/LeftIcon';
import RightIcon from '../Start/RightIcon';

import "./styles.css";

const StartAll: React.FC<any> = ({ history, dispatch, user }) => {
  const [loading, setLoading] = useState(true);
  const [loadingNews, setLoadingNews] = useState(true);
  const [listEnded, setListEnded] = useState(false);

  const [error, setError] = useState('');
  const [blocks, setBlocks] = useState([]);
  const [start, setStart] = useState(0);
  
  const [fetchUser] = useMutation(FETCH_USER);
  const [fetchFeedAll] = useMutation(FETCH_FEED_ALL);

  const listRef = useRef(null);
  
  useEffect(() => {
    userVerify();
  }, []);

  useEffect(() => {
    if(!listRef) return;
    if(!listRef.current) return;
    /* @ts-ignore */
    listRef.current.addEventListener('scroll', (e) => {
      const el = e.target;
      /* @ts-ignore */
      if(el.scrollTop + el.clientHeight === el.scrollHeight) load();
    });  
  }, [listRef.current]);

  const userVerify = async () => {
    if(!localStorage.getItem('token')) history.push('/');
    try {
      let account = user;

      if(!account) {
        const responseWebAccount = await fetchUser();
        const { userMutation } = responseWebAccount.data;
        account = userMutation;
      }

      const responseFeed = await fetchFeedAll({ variables: { start } });
      const { feedAllMutation } = responseFeed.data;

      setStart(6);

      dispatch(setUser(account));
      setBlocks(feedAllMutation.blocks);

      if(feedAllMutation.blocks.length < 6) setListEnded(true);

      setLoading(false);
      setLoadingNews(false);      
    } catch(error: any) {
      setLoading(false); 
      setError('Erro ao conectar com o servidor');
    }
  }

  const load = async () => {
    if(loadingNews) return;
    if(listEnded) return;
    setLoadingNews(true);
    try {
      const responseFeed = await fetchFeedAll({ variables: { start } });
      const { feedAllMutation } = responseFeed.data;

      setStart(start + 6);
      /* @ts-ignore */
      setBlocks([...blocks, ...feedAllMutation.blocks]);

      if(feedAllMutation.blocks.length < 6) setListEnded(true);

      setLoadingNews(false);      
    } catch(error: any) {
      setLoadingNews(false); 
      setError('Erro ao conectar com o servidor');
    }
  }

  if(error) return <ErrorPage text={error} />;
  if(loading) return <LoadingPage />;
  
  return (
    <div className={`basic-screen-inner start`} ref={listRef}>
      { blocks.map((block: BlockFeed) => 
        <Block>
          <div className="block-start-all">
            <BlockTitle title={block.name} />
            <Link to={`/topico/${block.topic_id}`}>Ver Mais</Link>
          </div>
          <ScrollingCarousel
            className="courses-swiper"
            leftIcon={<LeftIcon />}
            rightIcon={<RightIcon />}
          >
            { block.courses.map((course: Course) => 
              <CourseItemList
               key={`courseitemlist${course.id}${Math.random()}`}
               id={course.id.toString()}
               image={course.course_image}
               title={course.name}
               copy={course.copy}
               institute={course.institute}
               percentageWatched={course.percentageWatched ? course.percentageWatched : 0} />) }
          </ScrollingCarousel>
        </Block>
       ) }
    </div>
  );
}

const mapStateToProps = (state: any) => {
  const { user: { idCampus, user } } = state;
  return { idCampus, user };
};

export default connect(mapStateToProps)(StartAll);