import { useMutation } from '@apollo/client';
import React from 'react';
import { MdCloudDownload } from 'react-icons/md';
import { Link } from 'react-router-dom';
import { BasicScreenInner } from '../../components/BasicScreen';
import { Block, BlockTitle } from '../../components/Block';
import { Button, Checkbox } from '../../components/Form';
import Playlist from '../../components/Playlist';
import { WATCH_LESSON } from '../../graphql/mutations';
import { Attachment, Content, Course } from '../../types/Course';
import youtubeParser from '../../utils/youtubeParser';

import './styles.css';

interface DownloadResourceProps {
  name: string,
  link: string
};

export interface VideoProps {
  content: Content,
  course: Course | null,
  onContentClick: (content: Content) => void,
  onWatchChange: (watched: boolean) => void
}

const DownloadResource: React.FC<DownloadResourceProps> = ({ name, link }) => {
  /* @ts-ignore */
  const openLink = () => window.open(link, '_blank').focus();
  return (
    <div className="download-resource" onClick={openLink}>
      <MdCloudDownload size="20" />
      <span>{ name }</span>
    </div>
  );
}

const Video: React.FC<VideoProps> = ({ content, course, onContentClick, onWatchChange }) => {
  const [watchLesson] = useMutation(WATCH_LESSON);

  const onWatchClick = async () => {
    try {
      onWatchChange(!content.watched);
      watchLesson({ variables: { lesson_id: Number(content.lesson?.id), course_id: Number(course?.id) } });
      let nextContent;
      let isContent = false;
      course?.blocks.forEach((block) => {
        block.contents.forEach((nContent) => {
          if(isContent) {
            nextContent = nContent;
            isContent = false;
          }
          if(nContent.id === content.id) isContent = true
        })
      })
      if(nextContent)
        onContentClick(nextContent)
    } catch(error) {
      console.log(error);
    }
  }

  /* @ts-ignore */
  const youtubeLink: string = content.lesson?.youtube_link;

  return (
    <BasicScreenInner className="video">
      <Block className="video-block">
        <div className="video-iframe-container">
          <iframe
            src={`https://www.youtube.com/embed/${youtubeParser(youtubeLink)}`}
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            allowFullScreen={true}></iframe>
        </div>
        <div className="video-block-info">
          <div className="video-block-title">
            <BlockTitle title={content.name} />
            <Button name={content.watched ? `Marcar como Não Asssistido` : 'Continuar'} onClick={onWatchClick} />
          </div>
          { content.lesson?.description ? <hr /> : null }
          <p>
          { content.lesson?.description }
          </p>
          <hr />
          <div className="video-block-info-resources">
            { content.lesson?.attachments.map((attachment: Attachment) => <DownloadResource key={`atach${attachment.id}`} name={attachment.name} link={attachment.link} />) }
          </div>
        </div>
      </Block>
      <Playlist className="video-playlist" blocks={course?.blocks} onContentClick={onContentClick} />
      
    </BasicScreenInner>
  );
}

export default Video;